import Vue from "vue";
import VueRouter from "vue-router";

import Home from "@v/Home.vue";
import Login from "@v/Login/Login.vue";
import Welcome from "@v/welcome/Welcome.vue";

// 
import Role from "@v/role/Role.vue";

// 
import UserIndex from "@v/user/Index.vue";
import UserList from "@v/user/UserList.vue";
import AddUser from "@v/user/AddUser.vue";

// 
import CollegeIndex from "@v/college/Index.vue";
import CollegeList from "@v/college/CollegeList.vue";
import AddCollege from "@v/college/AddCollege.vue";

// 
import MajorIndex from "@v/major/Index.vue";
import MajorList from "@v/major/MajorList.vue";
import AddMajor from "@v/major/AddMajor.vue";

// 
const routerPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(err => err);
};
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home,
    redirect: "/welcome",
    children: [
      {
        path: "/welcome",
        name: "welcome",
        meta: {
          name: ""
        },
        component: Welcome,
        children: []
      },
      {
        path: "/role",
        name: "role",
        meta: {
          name: ""
        },
        component: Role,
        children: []
      },
      {
        path: "/user",
        name: "user",
        meta: {
          name: ""
        },
        component: UserIndex,
        redirect: "/user/userList",
        children: [
          {
            path: "/user/userList",
            meta: {
              name: ""
            },
            component: UserList,
            children: []
          },
          {
            path: "/user/userAdd",
            meta: {
              name: ""
            },
            component: AddUser
          }
        ]
      },
      {
        path: "/college",
        name: "college",
        meta: {
          name: ""
        },
        component: CollegeIndex,
        redirect: "/college/list",
        children: [
          {
            path: "/college/list",
            meta: {
              name: ""
            },
            component: CollegeList,
            children: []
          },
          {
            path: "/college/add",
            name: "collegeAdd",
            meta: {
              name: ""
            },
            component: AddCollege
          },
          {
            path: "/college/edit",
            name: "collegeEdit",
            meta: {
              name: ""
            },
            component: AddCollege
          }
        ]
      },
      {
        path: "/major",
        name: "major",
        meta: {
          name: ""
        },
        component: MajorIndex,
        redirect: "/major/list",
        children: [
          {
            path: "/major/list",
            meta: {
              name: ""
            },
            component: MajorList,
            children: []
          },
          {
            path: "/major/add",
            name: "majorAdd",
            meta: {
              name: ""
            },
            component: AddMajor
          },
          {
            path: "/major/edit",
            name: "majorEdit",
            meta: {
              name: ""
            },
            component: AddMajor
          }
        ]
      },

      {
        name: "404",
        path: "/404",
        component: () => import("@c/notFound/NotFound.vue")
      }
    ]
  },

  {
    path: "/login",
    name: "login",
    component: Login
  },

  {
    path: "*", // 
    redirect: "/404"
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
