<template>
  <div class="user-add">
    <el-tabs v-model="activeName">
      <el-tab-pane label="" name="first">
        <UserForm></UserForm>
      </el-tab-pane>
      <el-tab-pane label="" name="second">
        <div class="user-add-header cl">
          <el-upload
            :disabled="false"
            action="customize"
            name="openFile"
            :http-request="uploadFile"
            :file-list="file"
            :show-file-list="false"
            class="fr"
          >
            <el-button type="primary" class="fr" icon="el-icon-bottom-right"
              ></el-button
            >
          </el-upload>
          <el-button type="text" class="fr"></el-button>
        </div>
        <div class="user-add-content">
          <div>
            <el-button type="text" class="fl all" @click="allDelete"
              ></el-button
            >
          </div>
          <el-table
            ref="multipleTable"
            :data="userList"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange"
          >
            <el-table-column type="selection" width="50"> </el-table-column>
            <el-table-column
              label=""
              width="50"
              type="index"
            ></el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope"
                ><el-input v-model="scope.row.user_username"></el-input
              ></template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-input v-model="scope.row.user_password"></el-input>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-select
                  v-model="scope.row.role_id"
                  placeholder=""
                  style="width:100%"
                >
                  <el-option label="" :value="0"></el-option>
                  <el-option label="" :value="1"></el-option>
                  <el-option label="" :value="2"></el-option>
                  <el-option label="" :value="3"></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-input v-model="scope.row.user_name"></el-input>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-input v-model="scope.row.user_last_name"></el-input>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-radio v-model="scope.row.user_sex" label=""></el-radio>
                <el-radio v-model="scope.row.user_sex" label=""></el-radio>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="100" align="center">
              <template slot-scope="scope">
                <el-input v-model.number="scope.row.user_age"></el-input>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-select
                  v-model="scope.row.user_nation"
                  placeholder=""
                  style="width:100%"
                >
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-input v-model="scope.row.user_tel"></el-input>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-date-picker
                  v-model="scope.row.user_birthday"
                  type="date"
                  placeholder=""
                  style="width:100%"
                >
                </el-date-picker>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-input v-model="scope.row.user_address"></el-input>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-input v-model="scope.row.user_heath"></el-input>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-select
                  v-model="scope.row.user_culture"
                  placeholder=""
                  style="width:100%"
                >
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column label="" width="100" fixed="right">
              <template slot-scope="scope">
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  circle
                  @click="addOrRemove(scope, true)"
                  :disabled="userList.length === 1"
                ></el-button>
                <el-button
                  type="primary"
                  icon="el-icon-plus"
                  size="mini"
                  circle
                  v-if="scope.$index === userList.length - 1"
                  @click="addOrRemove(scope, false)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="user-add-footer">
          <el-button type="primary"></el-button>
          &nbsp;&nbsp;
          <el-button @click="resetClick"></el-button>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import UserForm from "./UserForm";
import XLSX from "xlsx";
export default {
  data() {
    return {
      userList: [{}],
      multipleSelection: [],
      activeName: "second",
      file: []
    };
  },
  methods: {
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 
    addOrRemove(scope, flag) {
      if (flag) {
        // 
        if (this.userList.length !== 1) {
          this.userList.splice(scope.$index, 1);
        }
      } else {
        // 
        this.userList.push({});
      }
    },
    // 
    allDelete() {
      const res = this.userList.filter(
        item => this.multipleSelection.indexOf(item) === -1
      );
      this.userList = res;

      if (this.userList.length === 0) {
        this.userList.push({});
      }
    },
    // 
    async uploadFile(params) {
      const _file = params.file;
      const fileReader = new FileReader();
      fileReader.onload = ev => {
        try {
          const data = ev.target.result;
          const workbook = XLSX.read(data, {
            type: "binary"
          });
          for (let sheet in workbook.Sheets) {
            //
            const sheetArray = XLSX.utils.sheet_to_json(workbook.Sheets[sheet]);
            const res = sheetArray.map(item => {
              return {
                user_username: item[""],
                user_password: item[""],
                role_id: item[Object.keys(item)[2]],
                user_name: item[""],
                user_sex: item[""],
                user_age: item[""],
                user_address: item[""],
                user_nation: item[""],
                user_tel: item[""],
                user_birthday: this.$.formatDate(item[""]),
                user_last_name: item[""],
                user_heath: item[""],
                user_culture: item[""]
              };
            });
            this.userList.push(...res);
          }
        } catch (e) {
          this.$message.warning("");
        }
      };
      fileReader.readAsBinaryString(_file);
    },
    // 
    resetClick() {
      this.userList = [{}];
      this.multipleSelection = [];
    }
  },
  components: {
    UserForm
  }
};
</script>

<style lang="scss" scoped>
@import "./AddUser.scss";
</style>
