<template>
  <div class="user-list">
    <div class="user-list-header">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="">
          <el-input v-model="formInline.user" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="formInline.region" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formInline.user" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="user-list-footer">
      <el-table :data="userList" stripe style="width: 100%" border>
        <el-table-column
          label="id"
          width="50"
          type="index"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="user_username"
          label=""
          align="center"
          min-width="150"
        >
        </el-table-column>
        <el-table-column
          prop="role"
          label=""
          align="center"
          min-width="80"
        ></el-table-column>
        <el-table-column
          prop="user_name"
          label=""
          align="center"
          min-width="150"
        ></el-table-column>
        <el-table-column
          prop="user_last_name"
          label=""
          align="center"
          min-width="150"
        ></el-table-column>
        <el-table-column
          prop="user_sex"
          label=""
          align="center"
          min-width="100"
        ></el-table-column>
        <el-table-column
          prop="user_age"
          label=""
          align="center"
          min-width="100"
        ></el-table-column>
        <el-table-column
          prop="user_nation"
          label=""
          align="center"
          min-width="100"
        ></el-table-column>
        <el-table-column
          prop="user_tel"
          label=""
          align="center"
          min-width="120"
        ></el-table-column>
        <el-table-column
          prop="user_birthday"
          label=""
          align="center"
          min-width="130"
        ></el-table-column>
        <el-table-column
          prop="user_address"
          label=""
          align="center"
          min-width="150"
        ></el-table-column>
        <el-table-column
          prop="user_heath"
          label=""
          align="center"
          min-width="100"
        ></el-table-column>
        <el-table-column
          prop="user_culture"
          label=""
          align="center"
          min-width="100"
        ></el-table-column>

        <el-table-column align="center" fixed="right" label="" width="120">
          <template slot-scope="scope">
            <el-button
              type="primary"
              @click="handleClick(scope.row, true)"
              icon="el-icon-edit"
              circle
              size="mini"
            ></el-button>
            <el-button
              type="danger"
              @click="handleClick(scope.row, false)"
              icon="el-icon-delete"
              circle
              size="mini"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        :hide-on-single-page="true"
        layout="prev, pager, next"
        :total="1000"
      >
      </el-pagination>
    </div>

    <el-dialog title="" :visible.sync="dialogVisible" width="70%">
      <userForm
        :dialogVisible="dialogVisible"
        :editUserObj="editUserObj"
        ref="userFormEle"
      ></userForm>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleCloseBtn(false)"> </el-button>
        <el-button type="primary" @click="handleCloseBtn(true)"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import userForm from "./UserForm";
export default {
  data() {
    return {
      formInline: {
        user: "",
        region: ""
      },
      userList: [
        {
          user_username: "user1",
          user_password: "123",
          role_id: 1,
          user_name: "",
          user_sex: "",
          user_age: 19,
          user_address: "",
          user_nation: "",
          user_tel: "13731002865",
          user_birthday: Date.now(),
          user_last_name: "",
          user_heath: "",
          user_culture: ""
        }
      ],
      dialogVisible: false,
      editUserObj: {}
    };
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    },
    handleClick(row, flag) {
      if (flag) {
        // 
        this.dialogVisible = true;
        this.editUserObj = Object.assign({}, row);
      } else {
        // 
        this.$confirm("?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            this.$message({
              type: "success",
              message: "!"
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      }
    },
    handleCloseBtn(flag) {
      if (flag) {
        this.$refs.userFormEle.onSubmit();
        const reuslt = this.$refs.userFormEle.isSuccess;
        if (reuslt) {
          this.dialogVisible = false;
        }
      } else {
        this.$refs.userFormEle.resetForm();
        this.dialogVisible = false;
      }
    }
  },
  components: {
    userForm
  }
};
</script>

<style lang="scss" scoped>
@import "./UserList.scss";
</style>
