<template>
  <div class="header">
    <div class="logo fl">
      <img src="@a/logo.png" alt="" srcset="" />
      <span></span>
    </div>
    <div class="user fr">
      <img src="@a/logo.png" alt="" srcset="" />
      <p>
        <span>mayifan</span
        ><el-button type="text" @click="open"></el-button>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    open() {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch();
    }
  }
};
</script>

