<template>
  <div>
    <el-form ref="userObj" :model="userObj" label-width="80px" :rules="rules">
      <el-row :gutter="40">
        <el-col :span="8" :offset="3">
          <el-form-item label="" prop="user_username">
            <el-input v-model="userObj.user_username"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="" prop="user_password" v-if="!dialogVisible">
            <el-input v-model="userObj.user_password" show-password></el-input>
          </el-form-item>
          <el-form-item label="" v-if="dialogVisible">
            <el-select
              v-model="userObj.user_culture"
              placeholder=""
              style="width:100%"
            >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="40">
        <el-col :span="8" :offset="3">
          <el-form-item label="" prop="role_id">
            <el-select
              v-model="userObj.role_id"
              placeholder=""
              style="width:100%"
            >
              <el-option label="" :value="0"></el-option>
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="2"></el-option>
              <el-option label="" :value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="">
            <el-input v-model="userObj.user_name"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="40">
        <el-col :span="8" :offset="3">
          <el-form-item label="">
            <el-input v-model="userObj.user_last_name"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="">
            <el-radio v-model="userObj.user_sex" label=""></el-radio>
            <el-radio v-model="userObj.user_sex" label=""></el-radio>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="40">
        <el-col :span="8" :offset="3">
          <el-form-item label="">
            <el-input
              type="age"
              v-model.number="userObj.user_age"
              autocomplete="off"
            ></el-input>
          </el-form-item>
        </el-col>

        <el-col :span="8">
          <el-form-item label="">
            <el-select
              v-model="userObj.user_nation"
              placeholder=""
              style="width:100%"
            >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="40">
        <el-col :span="8" :offset="3">
          <el-form-item label="">
            <el-input v-model="userObj.user_tel"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="">
            <el-date-picker
              v-model="userObj.user_birthday"
              type="date"
              placeholder=""
              style="width:100%"
            >
            </el-date-picker>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="40">
        <el-col :span="8" :offset="3">
          <el-form-item label="">
            <el-input v-model="userObj.user_address"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="">
            <el-input v-model="userObj.user_heath"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="40" v-if="!dialogVisible">
        <el-col :span="8" :offset="3">
          <el-form-item label="">
            <el-select
              v-model="userObj.user_culture"
              placeholder=""
              style="width:100%"
            >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row v-if="!dialogVisible">
        <el-col :span="21" :offset="3">
          <el-form-item>
            <el-button type="primary" @click="onSubmit"></el-button>
            <el-button @click="resetForm"></el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>

<script>
export default {
  props: ["dialogVisible", "editUserObj"],
  mounted() {
    if (this.editUserObj && this.editUserObj.role_id) {
      this.userObj = Object.assign({}, this.editUserObj);
    }
  },
  data() {
    return {
      userObj: {
        user_username: "",
        user_password: "",
        role_id: "",
        user_name: "",
        user_sex: "",
        user_age: "",
        user_address: "",
        user_nation: "",
        user_tel: "",
        user_birthday: Date.now(),
        user_last_name: "",
        user_heath: "",
        user_culture: ""
      },

      rules: {
        role_id: { required: true, message: "", trigger: "blur" },
        user_username: {
          required: true,
          message: "",
          trigger: "blur"
        },
        user_password: {
          required: true,
          message: "",
          trigger: "blur"
        }
      }
    };
  },
  methods: {
    onSubmit() {
      this.$refs["userObj"].validate(valid => {
        if (valid) {
          alert("submit!");
          this.isSuccess = true;
          return true;
        } else {
          this.isSuccess = false;
          return false;
        }
      });
    },
    resetForm() {
      this.$refs["userObj"].resetFields();
    }
  },
  watch: {
    editUserObj() {
      this.userObj = Object.assign({}, this.editUserObj);
    }
  }
};
</script>

<style lang="scss" scoped>
.el-form {
  padding-top: 30px;
  background-color: #fff;
}
</style>
